import Vue from 'vue';
import Vuex, { Store } from 'vuex';
Vue.use(Vuex);
var state = {
  enforced: false,
  enforcedGroups: [],
  excludedGroups: []
};
var mutations = {
  setEnforced: function setEnforced(state, enabled) {
    Vue.set(state, 'enforced', enabled);
  },
  setEnforcedGroups: function setEnforcedGroups(state, total) {
    Vue.set(state, 'enforcedGroups', total);
  },
  setExcludedGroups: function setExcludedGroups(state, used) {
    Vue.set(state, 'excludedGroups', used);
  }
};
export default new Store({
  strict: process.env.NODE_ENV !== 'production',
  state: state,
  mutations: mutations
});