        query: query
      })).then(function (res) {
        return res.data.ocs;
      }).then(function (ocs) {
        return ocs.data.groups;
      }).then(function (groups) {
        _this.groups = _.sortedUniq(_.uniq(_this.groups.concat(groups)));
      }).catch(function (err) {
        return console.error('could not search groups', err);
      }).then(function () {
        _this.loadingGroups = false;
      });
    }, 500),
    saveChanges: function saveChanges() {
      var _this2 = this;

      this.loading = true;
      var data = {
        enforced: this.enforced,
        enforcedGroups: this.enforcedGroups,
        excludedGroups: this.excludedGroups
      };
      axios.put(generateUrl('/settings/api/admin/twofactorauth'), data).then(function (resp) {
        return resp.data;
      }).then(function (state) {
        _this2.state = state;
